import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { EmpleadosService } from './empleados.service';
import { Empleado } from 'src/app/interface/empleado.interface';

@Component({
  selector: 'app-empleados',
  templateUrl: './empleados.component.html',
  styleUrls: ['./empleados.component.scss']
})
export class EmpleadosComponent implements OnInit {

  empleado : Empleado[]=[];
  empleados : Empleado[]=[];
  empleadoNew: Empleado = {
    id:"",
    brm:"",
    foto:"",
    nombre:"",
    puesto:""
  }
  empleadoMod: Empleado = {
    id:"",
    brm:"",
    foto:"",
    nombre:"",
    puesto:""
  }
   
  constructor(private empleadosService:EmpleadosService, private activatedRoute:ActivatedRoute) { 

    this.empleadosService.getEmpleados("")
    .subscribe(
      resp=> {
        this.empleados = resp;
      }
    );
    
  }

  ngOnInit() {
  }

  modificar(empleado:Empleado){
    // this.activatedRoute.params
    //   .pipe(
    //     switchMap(
    //       ({id})=> this.empleadosService.getEmpleadoPorId(empleado.id)
    //       )
    //   )
    //   .subscribe(empleado => this.empleadoMod = empleado)

    this.empleadosService.getEmpleadoPorId(empleado.id)
    .subscribe(
      resp=> this.empleadoMod = resp
    )

  }

  modificaM(){
    this.empleadosService.actualizarEmpleado(this.empleadoMod)
    .subscribe(resp =>{
        window.location.reload();
        alert('Empleado modificado');
      });
  }

  eliminar(empleado:Empleado){
    this.empleadosService.borrarEmpleado(empleado.id)
    .subscribe(resp=>{
      window.location.reload();
      alert('Empleado eliminado');
    });
  }

  guardar(){

    if(this.empleadoNew.nombre.trim().length === 0){
      return alert('Ingrese un nombre');
    }else if(this.empleadoNew.brm.trim().length === 0){
      return alert('Ingrese el BRM');
     }
     //else if(this.empleadoNew.foto.trim().length === 0){
    //   return alert('Seleccione una foto');
    // }

    this.empleadosService.agregarEmpleado(this.empleadoNew)
    .subscribe(resp=>{
      window.location.reload();
      alert('Empleado actualizado');
    })
  }

}
