import { Component, OnInit } from '@angular/core';
import { Usuario } from 'src/app/interface/usuario.interface';
import { AlertService, ConfirmationService } from '@next/nx-controls-common';
import { AutenticarService } from './autenticar.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-autenticacion',
  templateUrl: './autenticacion.component.html',
  styleUrls: ['./autenticacion.component.scss']
})
export class AutenticacionComponent implements OnInit {

  usarioVal : Usuario ={
    userId: "",
    password: ""
  }

  constructor(private confirmationService: ConfirmationService, private alertService: AlertService, private loginService: AutenticarService, private route: Router) { }

  ngOnInit() {
  }


  //INGRESAR
  ingresa(){
    
    this.loginService.validaUsuario(this.usarioVal)
    .subscribe(resp => {
      console.log(resp);
      this.success("Bienvenido: " + resp.userId + "!!");
      if(resp.userId == "blanca"){  
        this.route.navigate(['/','game'])
      }else
      if(resp.userId == "viridiana"){  
        this.route.navigate(['/','empleados'])
      }else{
        this.route.navigate([''])
      }
    },
    err => {
      this.error("Usuario o contraseña incorrectos");
    }
      )
    // this.error("error :(");
  }


  //Confirm:
  confirmEvent() {
    this.confirmationService.confirm({
      message: '¿Esta seguro de querer realizar esta acción?',
      lblOkBtn: 'Si',
      lblCancelBtn: 'Cancelar',
      accept: () => {
          console.log('Saved!');
      },
      reject: () => {
          console.log('Canceled!');
      }
    });
  }


  //Alertas:

  success(message: string) {
    this.alertService.success(message);
  }

  error(message: string) {
      this.alertService.error(message);
  }

  info(message: string) {
      this.alertService.info(message);
  }

  warn(message: string) {
      this.alertService.warn(message);
  }

  clear() {
      this.alertService.clear();
  }

}