import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Empleado } from 'src/app/interface/empleado.interface';


@Injectable({
  providedIn: 'root'
})
export class EmpleadosService {

  private urlEndPoint:string = "https://aplicacionempresarialviri.herokuapp.com/api";
  public resultados:Empleado[]=[];

  constructor(private http:HttpClient) { }

  //OBTIENE TODOS LOS EMPLEADOS
  getEmpleados(nombre:String){
    return this.http.get<Empleado[]>(this.urlEndPoint + "/empleados");
  }

  //OBTIENE UN EMPLEADO POR ID
  getEmpleadoPorId(id:String){
    return this.http.get<Empleado>(this.urlEndPoint + "/empleados/" + id);
  }

  //BORRA UN EMPLEADO POR ID
  borrarEmpleado(id:String){
    return this.http.delete<Empleado>(this.urlEndPoint + "/empleados/" + id);
  }

  //ACTUALIZA UN EMPLEADO POR ID
  actualizarEmpleado(empleado:Empleado):Observable<Empleado>{
    return this.http.put<Empleado>(this.urlEndPoint + "/empleados/" + empleado.id, empleado);
  }

  //AGREGA UN NUEVO EMPLEADO
  agregarEmpleado(empleado:Empleado):Observable<Empleado>{

    return this.http.post<Empleado>(this.urlEndPoint + "/empleados", empleado);
  }

}