import { Component, OnInit } from '@angular/core';
import { ConfirmationService, AlertService } from '@next/nx-controls-common';

@Component({
  selector: 'app-confirmation',
  templateUrl: './confirmation.component.html',
  styleUrls: ['./confirmation.component.scss']
})
export class ConfirmationComponent implements OnInit {

  constructor(
    private confirmationService: ConfirmationService,
    private alertService: AlertService
  ) { }

  ngOnInit() {
  }

  confirmar() {
    this.confirmationService.confirm({
      message: '¿Está seguro de querer realizar esta acción?',
      lblOkBtn: 'Si',
      lblCancelBtn: 'No',
      accept: () => this.alertService.success('Confirmación éxitosa'),
      reject: () => this.alertService.warn('Confirmación cancelada')
    });
  }

}
